import "./styles.css";
import { Card } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import { useEffect, useState } from "react";
export default function App() {
  const [d, sd] = useState();
  const fetchApi = async (movieName) => {
    const url = `https://fake-movie-database-api.herokuapp.com/api?s=${movieName}`;
    const res = await fetch(url);
    const da = await res.json();
    sd(da.Search);
  };
  useEffect(() => {
    fetchApi("batman");
  }, []);
  console.log(d);
  return (
    <div className="App">
      {d &&
        d.map((i) => (
          <Card>
            <Card.Body>
              <Display d={i} />
            </Card.Body>
          </Card>
        ))}
    </div>
  );
}

const Display = ({ d }) => {
  return (
    <>
      <div>
        <span>{d.Title}</span>
        <img style={{ width: "5rem" }} src={d.Poster} alt="" />
      </div>
    </>
  );
};
